@import "color.less";


.shopping-cart--product {
    //background-color: @white;
    color: @darkest_gray;
    min-height: 50px;
    padding:5px;
}

.row{
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.flexible-row{
  // margin-left:-5px;
  // margin-right: -5px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

[class*="col-"]:not(.icon-left-container):not(.icon-right-container):not(.quantity){
  flex:1 1 auto;
  word-wrap: break-word;
}

flexible-col{
  flex:1 1 auto;
  word-wrap: break-word;
}

.quantity{
  flex: 0 0 auto;
  background-color: @dark_cyan;
  color: @white;
  font-size: 16px;
  height:24px;
  padding-left: 0px !important;
  padding-right: 0px !important;
  border-radius: 12px;
  line-height: 24px;
  text-align: center;
}



.icon-left-container{
  flex:0 1 auto;
  max-width: 35px;
  padding-right: 5px!important;
  text-align: center;
}
.icon-right-container{
  flex: 0 1 auto;
  max-width:35px;
  padding-left: 5px!important;
  text-align: center;
}

.center-container{
  flex: 1 1 auto; 
  max-width:100%;
  padding: 0!important;
}

.cell__primary {
    font-weight: bold;
}
.cell__uppercase {
    text-transform: uppercase;
}
.cell__font-md {
    font-size: 14px;
}
.cell__font-sm {
    font-size: 12px;
}
.cell__stroke {
    text-decoration: line-through;
}
.cell__text-right {
    text-align: right;
}
  


  .cell__icon {
    font-size: 25px;
    color: @green;
    cursor: pointer;
    &::before{
      vertical-align: middle;
    }
  }

.firstLine {
    overflow: hidden;
}

.secondLine {
    overflow: hidden;
}

.promo{
  color:@green;
  text-transform: uppercase;
  font:bold 12px arial;
  line-height: 18px;
  padding-right:0px !important;
  cursor: pointer;
  .icon-arrow-right:before {
    text-align: right;
    vertical-align: middle;
    font-size:1em !important;
    font-weight: 900 !important;
  }
}
.promo-desc{
  color:@lightest_blue;
  letter-spacing: -0.1px;
  font:bold 12px arial;
  line-height: 16px;
  text-transform: capitalize;
}

.covenant, .covenant-desc{
text-transform: uppercase;
 font:bold 12px arial;
 line-height: 16px;
}

.covenant-colour{
  color: @violet;
}

